  position: fixed;
  bottom: 60px;
  right: -40px;
  box-shadow: 0 0 0 3px #0003;
}

#toTop:hover {
  background: #ff6b08;
}

#toTop svg {
  fill: #fff;
  transition: all .3s ease-in-out;
  position: relative;
  top: 12px;
  right: -1px;
}

